import React, { Component } from 'react'
import Dish from '../dish/Dish'
import './styles.css'

class DishList extends Component {

    render() {

        const showImages = () => {
            return this.props.link.map(dish => (
                <Dish link={dish.strMealThumb} 
                    key={dish.idMeal}
                    name={dish.strMeal}
                    id={dish.idMeal} 
                />)
            )
        }

        return (
            <div className="dish-list">
                { showImages() }
            </div>
        )
    }
}

export default DishList
