import React from 'react'
import './cartItem.styles.css'

function CartItem({ link, name, price, qty, addItem, removeItem, id }) {

    const totalPrice = +price.slice(1) * +qty

    return (
        <div className="item">
            <img src={link} alt="Img" className="item-thumb" />
            <div className="item-name">{name}</div>
            <div className="item-quantity-container">
                <button className="item-quantity-decrease" onClick={ () => removeItem(id) }>-</button>
                <div className="item-quantity">{qty}</div>
                <button className="item-quantity-increase" onClick={ () => addItem(id) }>+</button>
            </div>
            <div className="item-price">{price}</div>
            <div className="item-total-price">${totalPrice}</div>
        </div>
    )
}

export default CartItem
