import React, { Component } from 'react'
import { Route } from 'react-router-dom'
import Homepage from './pages/homepage/Homepage';
import ShoppingCart from './pages/shopping-cart/ShoppingCart';
import './App.css'

class App extends Component {

  constructor() {
    super();

    this.state = {
      cart: []
    }
  }

  dishDataCallback = (name, price, link, id) => {

    const newDish = { name, price, link, id }
    const exists = this.state.cart.find(x => x.id === newDish.id)

    if (!exists) {

      this.setState({
        cart: [...this.state.cart, { ...newDish, qty: 1 }]
      })

    }
  }

  addItem = (id) => {

    const newArr = this.state.cart.map(x => {

      if (x.id === id) return { ...x, qty: x.qty + 1 };
      return x;

    })

    this.setState({ cart: newArr })
  }

  removeItem = (id) => {

    const newArr = this.state.cart.map(x => {

      if (x.id === id && x.qty > 1) return { ...x, qty: x.qty - 1 };
      return x;

    })

    this.setState({ cart: newArr })
  }

  render() {

    return (
      <div className="App">
        <DishContext.Provider value={this.dishDataCallback}>
          <Cart.Provider value={[this.state.cart, this.addItem, this.removeItem]}>
            <Route exact path="/Gourmet-World/" component={Homepage} />
            <Route exact path="/Gourmet-World/shopping-cart" component={ShoppingCart} />
          </Cart.Provider>
        </DishContext.Provider>
      </div>
    )
  }
}

export default App;
export const DishContext = React.createContext();
export const Cart = React.createContext();
