import React from 'react'
import './footer.styles.css'

function Footer() {
    return (
        <>
            <img src="/Gourmet-World/icons/footer-wave.svg" alt="" className="footer-wave" />
            <footer className="footer">
                <div className="footer-title">Gourmet World!</div>
                <div className="footer-container">

                    <div className="footer-container-1">
                        <div className="footer-items">Links</div>
                        <div className="footer-items">About</div>
                        <div className="footer-items">FAQ</div>
                        <div className="footer-items">Contacts</div>
                    </div>

                    <div className="footer-container-2">
                        <div className="footer-items">Services</div>
                        <div className="footer-items">Pre-Order food</div>
                        <div className="footer-items">Food for Takeaway</div>
                        <div className="footer-items">Food Delivery</div>
                    </div>

                    <div className="footer-container-3">
                        <div className="footer-items">Follow us</div>
                        <div className="footer-items">Facebook</div>
                        <div className="footer-items">Twitter</div>
                        <div className="footer-items">Instagram</div>
                    </div>

                </div>
                <img src="logo.png" alt="I love cooking!" className="footer-chef" />
            </footer>
        </>
    )
}

export default Footer
